body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}

.container {
  width: 100vw;
  height: 100vh;
  box-sizing:border-box;
  /* padding: 25px; */
  border: 25px solid #fff;
  position:relative;
}
.anim {
    position: relative;
    /* transition: background-image 1s linear; */
}

.arrow, .play-pause {
    cursor: pointer;
    color: #fff;
}

.arrow {
    font-size: 80px;
}

.arrow-right {
    position: absolute;
    right: 40px;
    top: 45%;
}

.arrow-left{
    position: absolute;
    left: 40px;
    top: 45%;
}

.play-pause {
    font-size: 40px;
    position: absolute;
    left: 15px;
    bottom: 15px;
}
.header {
    width: 100%;
}

.logo-box {
    position: absolute;
    top: 15px;
    left: 15px;
    background: #fff;
    height: 100px;
    width: 100px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    cursor: pointer;
  }
  
  .logo-img {
    height: 60%;
  }
  
  .menu-button {
    position: absolute;
    top:15px;
    right:15px;
    background: rgba(255, 255, 255, 0.897);
    border: 1px solid rgba(53, 53, 53, 0.185);
    font-family: 'Open Sans Condensed', sans-serif;
    font-size: 20px;
    width: 100px;
    height:30px;
    cursor: pointer;
    text-transform: uppercase;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
  }
  
  
  .menu-text, .menu-icon{
    color: rgba(53, 53, 53, 0.466);
  }

  .menu-text {
      letter-spacing: 3px;
  }
  

  .menu {
      position: fixed;
      top: 0;
      left: 0;
      background-color: #fff;
      width: 100vw;
      height:100vh;
      transition: opacity .5s ease-out;
  }

  .menu-items {
      list-style-type: none;
      text-transform: uppercase;
      position: absolute;
      top:50%;
      left: 50%;
      -webkit-transform: translate(-50%, -50%);
              transform: translate(-50%, -50%);
      font-family: 'Open Sans Condensed', sans-serif;
      font-size: 25px;
      text-align: center;
  }

  .menu-item {
    
    letter-spacing: 5px;
  }


  li{
    margin-bottom: 15px;
    opacity: 0.7;
    color: #000;
    transition: opacity .2s linear;


  }

  li:hover {
      opacity: 1;
  }

  .menu-item, .menu-item:link, .menu-item:visited {     
      text-decoration: none;
  }
